import { AuthContext } from "../../contexts/AuthContext";
import { useState, useContext } from "react";
import axios from "axios";

const LoginCard = () => {
  const authContext = useContext(AuthContext);
  const [loginForm, setLoginForm] = useState({});

  const onChange = (e) => {
    const { value, name } = e.target;
    setLoginForm({
      ...loginForm,
      [name]: value,
    });
  };

  const onSubmit = async (e) => {
    e.preventDefault();

    try {
      const response = await axios.post("/auth/login", loginForm);
      const { token, user, expiration } = response.data;

      alert("Bem vindo de volta, " + user.user);
      window.location.href = "/";
      authContext.login(token, expiration, user, user.clientInfo);
    } catch (err) {
      alert(
        `Nome de usurio ou senha errados. Confira as informaes, e tente novamente. (error: ${err})`
      );
    }
  };

  return (
    <>
      <div className="container container-fluid">
        <div className="row">
          <div className="col"></div>
          <div className="col-8">
            <div className="card">
              <div className="card-title">Login</div>
              <div className="card-body">
                <div className="card-text">Bem vindo!</div>
                <form
                  onSubmit={(e) => {
                    onSubmit(e);
                  }}
                >
                  <label htmlFor="email">E-mail:</label>
                  <input
                    type="text"
                    name="email"
                    value={loginForm.email}
                    onChange={onChange}
                  />
                  <br />
                  <label htmlFor="password">Senha:</label>
                  <input
                    type="password"
                    name="password"
                    value={loginForm.value}
                    onChange={onChange}
                  />
                  <br />
                  <button type="submit" className="btn btn-lg btn-primary">
                    Entrar
                  </button>
                </form>
              </div>
            </div>
          </div>
          <div className="col"></div>
        </div>
      </div>
    </>
  );
};

export default LoginCard;
