const createGoogleAuthLink = async () => {
  try {
    const request = await fetch("/auth/google-auth/createAuthLink", {
      method: "POST",
    });
    const response = await request.json();
    window.location.href = response.url;
  } catch (error) {
    console.log("App.js 12 | error", error);
    throw new Error("Issue with Login", error.message);
  }
};

const handleTokenFromQueryParams = () => {
  const query = new URLSearchParams(window.location.search);
  const accessToken = query.get("accessToken");
  const refreshToken = query.get("refreshToken");

  if (accessToken && refreshToken) {
    const expirationDate = newExpirationDate();
    console.log("App.js 30 | expiration Date", expirationDate, accessToken);

    storeTokenData(accessToken, refreshToken, expirationDate);
    window.location.href = "/";
  }
};

const newExpirationDate = () => {
  var expiration = new Date();
  expiration.setHours(expiration.getHours() + 1);
  return expiration;
};

const storeTokenData = async (token, refreshToken, expirationDate) => {
  sessionStorage.setItem("accessToken", token);
  sessionStorage.setItem("refreshToken", refreshToken);
  sessionStorage.setItem("expirationDate", expirationDate);
};

const handleTokenFromStorage = async () => {
  const storagedAccessToken = sessionStorage.getItem("accessToken");
  if (!!storagedAccessToken) {
    return storagedAccessToken;
  }
};

export {
  handleTokenFromQueryParams,
  createGoogleAuthLink,
  storeTokenData,
  handleTokenFromStorage,
};
