import { useContext } from "react";
import { Link } from "react-router-dom";

import { AuthContext } from "../../contexts/AuthContext";

import "./navbar.css";

const Navbar = () => {
  const authContext = useContext(AuthContext);

  function togglerNavHandler(e) {
    var element = document.getElementById("navbarNavDropdown");
    element.className = "collapse navbar-collapse";
  }

  return (
    <nav class="navbar navbar-expand-lg navbar-light bg-light main-navbar">
      <div class="container-fluid">
        <a class="navbar-brand" href="/">
          Agendador
        </a>
        <button
          class="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>

        <div
          class="collapse navbar-collapse nav-links"
          id="navbarSupportedContent"
        >
          <ul class="navbar-nav">
            <li class="nav-item">
              <Link to="/" class="nav-link active" onClick={togglerNavHandler}>
                Home
              </Link>
            </li>
            <li class="nav-item">
              <Link
                to="/conheca"
                class="nav-link active"
                onClick={togglerNavHandler}
              >
                Conhea
              </Link>
            </li>
            {authContext.isLoggedIn ? (
              <>
              <li class="nav-item">
                  <Link
                    to="/configuracoes"
                    class="nav-link active"
                    onClick={togglerNavHandler}
                  >
                    Configuraes
                  </Link>
                </li>
                <li class="nav-item">
                  <Link
                    to="/"
                    class="nav-link active"
                    onClick={e => authContext.logout()}
                  >
                    Sair
                  </Link>
                </li>
              </>
            ) : (
              <li class="nav-item">
                <Link
                  to="/login"
                  class="nav-link active"
                  onClick={togglerNavHandler}
                >
                  Entrar
                </Link>
              </li>
            )}
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
