const handleLocalStoreToken = async (token, expiration, user) => {
  if (!!token) {
    localStorage.setItem("token", token);
    localStorage.setItem(
      "tokenExpirationTime",
      new Date(expiration).toISOString()
    );
    localStorage.setItem("user", JSON.stringify(user));
  }
};

const handleLocalStoragedToken = async () => {
  const storedTokenData = localStorage.getItem("token");
  const storedTokenExpirationTimeData = localStorage.getItem(
    "tokenExpirationTime"
  );
  const storedUserData = JSON.parse(localStorage.getItem("user"));

  if (storedTokenData && storedTokenExpirationTimeData && storedUserData) {
    const expiration = new Date(storedTokenExpirationTimeData);

    return {
      token: storedTokenData,
      expiration: expiration,
      user: storedUserData,
    };
  }
};

const handleExpiredToken = (token, tokenExpirationTime) => {
  if (token && tokenExpirationTime) {
    const remainingTime = tokenExpirationTime.getTime() - new Date().getTime();
    var logoutTimer = setTimeout(remainingTime);
  } else {
    clearTimeout(logoutTimer);
  }
};

export { handleLocalStoragedToken, handleLocalStoreToken, handleExpiredToken };
