import { useState, useEffect, useContext } from "react";
import { Link } from "react-router-dom";

import { GoogleAuthContext } from "../../contexts/AuthContext";

const GoogleLogin = () => {
  const googleAuthContext = useContext(GoogleAuthContext);

  return (
      <div className="row">
        <div className="col-12 main-buttons">
          {!googleAuthContext.isLoggedIn ? (
            <button onClick={e => googleAuthContext.googleLogin()} className="btn btn-secondary">
              Login
            </button>
          ) : (
            <>
              <button onClick={googleAuthContext.googleLogout} className="btn btn-secondary">
                Sign Out
              </button>
              <div className="container-fluid content-container"></div>
            </>
          )}
        </div>
      </div>
  );
};

export default GoogleLogin;
