import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import { useState, useEffect, useContext } from "react";

import Landing from "./pages/Landing";
import Authentication from "./pages/Authentication";
import Configuration from "./pages/Configuration";

import Navbar from "./components/navbar/Navbar";
import GoogleLogin from "./components/googleLogin/GoogleLogin";
import CreateServiceForm from './pages/novo-servico/CreateServiceForm';


import { AuthContext, GoogleAuthContext } from "./contexts/AuthContext";

import {
  handleLocalStoragedToken,
  handleLocalStoreToken,
  handleExpiredToken,
} from "./utils/tokens-handler";

import {
  createGoogleAuthLink,
  handleTokenFromQueryParams,
  handleTokenFromStorage
} from './utils/auth.google'

import "./App.css";
import axios from "axios";

function App() {
  const [token, setToken] = useState(false);
  const [tokenExpirationTime, setTokenExpirationTime] = useState(false);
  const [user, setUser] = useState({});
  const [clientId, setClientId] = useState("");
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [isGoogleLoggedIn, setIsGoogleLoggedIn] = useState(false);

  const login = (token, expiration, user) => {
    setToken(token);
    setTokenExpirationTime(expiration);
    setUser(user);
    setIsLoggedIn(true);
    setClientId(user.clientInfo.clientId)
  
    handleLocalStoreToken(token, expiration, user);
  };

  const logout = () => {
    if (!window.confirm("Tem ceteza de que deseja sair?")) return;

    alert("Obrigado por utilizar nossos servios. Volte sempre.");
    setToken(null);
    setTokenExpirationTime(null);
    setIsLoggedIn(false);
    localStorage.clear();
  };

  const handleAuthLogin = async () => {
    const { token, expiration, user } = await handleLocalStoragedToken();
    login(token, expiration, user);
  };

  const handleClientId = async clientId => {
    setClientId(clientId);
    const saveClientId = await axios.post('/agendador/clients/client', {
      userId: user.userId, 
      clientId
    })
  }; 

  useEffect(() => {
    handleAuthLogin();
  }, []);

  useEffect(() => {
    handleExpiredToken();
  }, []);


// Google auth handlers
  const googleLogin = async () => {
    await createGoogleAuthLink().then(resp => {
      setIsGoogleLoggedIn(!!resp)
    });
  };

  const googleLogout = async () => {
    setIsLoggedIn(false);
    sessionStorage.clear();
  };

  useEffect(() => {
    handleTokenFromStorage().then(value => {
      setIsGoogleLoggedIn(!!value)
    });
  }, []);

  useEffect(() => {
    handleTokenFromQueryParams();
  }, []);

  return (
    <AuthContext.Provider
      value={{
        isLoggedIn: isLoggedIn,
        login: login,
        logout: logout,
        token: token,
        clientId: clientId,
        setClientId: handleClientId,
      }}
    >
      <GoogleAuthContext.Provider
        value={{
          isLoggedIn: isGoogleLoggedIn,
          googleLogin: googleLogin,
          googleLogout: googleLogout,
        }}
      >
        <div className="App">
          <Router>
            <Navbar />
            <Routes>
              <Route path="/" element={<Landing />}></Route>
              <Route path="/login" element={<Authentication />}></Route>
              <Route path="/configuracoes" element={<Configuration />}></Route>
              <Route path="/configuracoes/servicos/novo-servico" element={<CreateServiceForm />} />
            </Routes>
          </Router>
        </div>
      </GoogleAuthContext.Provider>
    </AuthContext.Provider>
  );
}

export default App;
