import axios from "axios";
import React, { useContext, useEffect, useState, useRef } from "react";
import { Editor } from "@tinymce/tinymce-react";
import { AuthContext } from "../../contexts/AuthContext";

const CreateServiceForm = () => {
  const [formValues, setFormValues] = useState({});
  const editorRef = useRef(null);
  const authContext = useContext(AuthContext);

  function onChange(e) {
    const { value, name } = e.target;

    setFormValues({
      ...formValues,
      [name]: value,
    });
  };

  const onEditorChange = () => {
    setFormValues({
      ...formValues,
      text: editorRef.current.getContent()
    })
  };

  const onFormSubmit = async (e) => {
    e.preventDefault();
    const { data } = await axios.post("/agendador/clients/create-service", {
      clientId: authContext.clientId,
      service: formValues,
    });
    console.log(data.update);
    if (data.update) {
      alert("Servio adicionado com sucesso!");
      window.location.href = "/configuracoes";
    }
  };

  return (
    <section id="service-form">
      <div className="container mt-4 mb-4 card">
        {/* <!--Bootstrap classes arrange web page components into columns and rows in a grid --> */}
        <div className="row justify-content-md-center ">
          <div className="col-md-12 col-lg-11 ">
            <h1 className="h2 mb-4 card-title mt-3">Adicionar Servico</h1>

            <form className="form-group" onSubmit={(e) => onFormSubmit(e)}>
              <div className="row">
                <div className="col-4">
                  <label className="mb-3">Ttulo do Servio</label>
                  <input
                    type="title"
                    className="form-control"
                    name="title"
                    value={formValues.title}
                    onChange={onChange}
                  />
                  <label className="mt-2">Categoria</label>
                  <input
                    type="text"
                    className="form-control"
                    name="categorie"
                    value={formValues.categorie}
                    onChange={onChange}
                  />
                  <label className="mt-2">Sub Categoria</label>
                  <input
                    type="text"
                    className="form-control"
                    name="subCategorie"
                    value={formValues.subCategorie}
                    onChange={onChange}
                  />
                  <label className="mt-2">Preo</label>
                  <input
                    type="number"
                    min="0.00"
                    max="100000.00"
                    step="0.01"
                    className="form-control"
                    name="price"
                    value={formValues.price}
                    onChange={onChange}
                  />
                  <label className="mt-3">Tempo de Durao (em minutos)</label>
                  <input
                    type="number"
                    min="1"
                    max="360"
                    step="1"
                    className="form-control"
                    name="durationTime"
                    value={formValues.durationTime}
                    onChange={onChange}
                  />
                  <label className="mt-3">URL (caminho) da imagem</label>
                  <input
                    type="url"
                    min="1"
                    max="360"
                    step="1"
                    className="form-control"
                    name="imageUrl"
                    value={formValues.imageUrl}
                    onChange={onChange}
                  />
                </div>
                <div className="col-8">
                  <label className="mb-3">Descrio do Servio</label>
                  <Editor
                    name="text"
                    onChange={onEditorChange}
                    onInit={(evt, editor) => (editorRef.current = editor)}
                    // initialValue={formValues.text}
                    // value={formValues.text}
                    init={{
                      height: 500,
                      menubar: false,
                      plugins: [
                        "advlist autolink lists link image charmap print preview anchor",
                        "searchreplace visualblocks code fullscreen",
                        "insertdatetime media table paste code help wordcount",
                      ],
                      toolbar:
                        "undo redo | formatselect | " +
                        "bold italic backcolor | alignleft aligncenter " +
                        "alignright alignjustify | bullist numlist outdent indent | " +
                        "removeformat | help",
                      content_style:
                        "body { font-family:Helvetica,Arial,sans-serif; font-size:14px }",
                    }}
                  />
                  <button type="submit" className="btn btn-primary mt-3 mb-3">
                    Salvar
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};

export default CreateServiceForm;
